.page__body {
  min-height: 100vh;

  background-color: $gray-600;
}

.page__wrap {
  display: grid;
  align-content: start;

  background-color: $white;

  grid-template-rows: min-content 1fr min-content;

  @media (min-width: $desktop) {
    max-width: 1200px;
    margin: 0 auto;

    box-shadow: 0 5px 15px $black-20;
  }
}
